body{
	height: 100vh;
	overflow: hidden;
}
p{
	margin: 0;
	padding: 0;
}
.demologo{
	position: absolute;
	z-index: 999;
	width:10%;
	margin-left: 50px;
	margin-top: -10px;
}
.menu-bar-main{
	position: absolute;	
	display: -ms-flexbox;	
	display: flex;
	-ms-flex-pack: end;
	    justify-content: flex-end;
	width: 100%;	
	text-align: center;	
	z-index: 99999;
	padding: 10px 30px;	
	-webkit-box-sizing: border-box;	
	        box-sizing: border-box;
}
.menu-bar-but{
	padding:3px 4px;
	margin: 0 2px;
}
.load-data-but{
	position: relative;
	text-align: center;	
}
.load-data-but:hover{
	cursor: pointer;
	border: 1px solid black;
	border-radius: 4px;
}
.load-data-but:hover .load-data-tooltip{
	visibility: visible;
}
.tooltip{
	position: absolute;	
	visibility: hidden;
	left: -3px;

}
.ldm-main,
.fdm-main,
.cpm-main{
	width: 150px;
	max-height: 70vh;	
	padding:0 0 15px 0;
	position: absolute;
	background-color: #eee;
	right: 10px;	
	margin-top:60px;
	border: 3px solid #09f;
	z-index: 9999;
	overflow-y: scroll;
	overflow-x: hidden;
}
.ldm-header,
.fdm-header,
.cpm-header{
	padding: 5px;	
	border-bottom: 2px solid #09f;
	margin: 0;	
}
.ldm-close,
.fdm-close,
.cpm-close{
	float: right;	
	color:red;
	margin-right: 5px;	
	font-weight: bolder;
	font-size: 20px;
}
.ldm-close:hover,
.fdm-close:hover,
.cpm-close:hover{
	cursor: pointer;
}
.ldm-list-container{	
	background-color: 	white;
	height: 80%;	
	margin: 20px 10px;
	border: 3px solid #aaa;
	overflow: auto;
}
.state-list-display{
	background-color: 	white;
	height: 100px;	
	margin: 10px 10px 5px 10px;
	border: 3px solid #09f;
	overflow: auto;
}
.state-list-display-li:hover{
	background-color: #5bf;
	cursor: pointer;
}
.ldm-li{
	padding: 2px 5px;
	font-size: small;

}
.ldm-li-tableName{
	text-decoration: underline;
	display: block;
}
.ldm-li:hover{
	background-color: 	#5bf;
	cursor: pointer;
}
.include-exclude-but, 
.add-state-but{
	width: 85%;
	margin: auto;
	color: white;
	padding: 1px 6px;
	text-align: center;
	background-color: #09f;
	border: 2px solid #09f;
	border-radius: 10px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	margin-bottom: 5px;
}

.include-exclude-but:hover,
.add-state-but:hover{
	border: 2px solid black;
	cursor: pointer;
}
.add-state-but:hover .state-select-container{
	display: block;
}
.add-state-but{
	position: relative;
}
.state-select-container{
	position: absolute;
	display: none;
	height: 200px;
	overflow: auto;
	margin: auto;
	color: white;
	padding: 1px 6px;
	text-align: center;
	background-color: #09f;
	border: 2px solid black;
	margin-bottom: 5px;
	left: 0;
}
.state-select-li{
	border-bottom: 1px solid black;
}
.state-select-li:hover{
	border-bottom: 1px solid white;
}
.fdm-input-container{
	font-size: 13px;
	width: 85%;
	margin: auto;
	text-align: center;
	border: 2px solid #09f;
	padding: 3px 0;
}
.fdm-input{
	width: 30px;
	margin-left: 3px;
}
.spacer{
	margin-top: 6px;
}
.set-filter-but{
	text-align: center;
	width: 85%;
	margin: auto;
	border: 2px solid #09f
}
.set-filter-but:hover{
	cursor: pointer;
	background-color: #09f;
	border: 2px solid black;
	color: white;
}
.flexbox-fix:nth-child(4){
	display: none !important;
}
.color-picker{
	width: 140px;
	margin: auto;
	text-align: center;
}



